.app-wrapper {
  display: grid;
  margin: 0 auto;
  grid-template-areas: 
  'header header'
  'navbar content';
  grid-template-rows: 90px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
 width: 1200px;
}
.app-wrapper-content {
  max-width: 100%;
  grid-area: content;
  background-color: rgb(148, 234, 245); 
}


